@import url('https://fonts.googleapis.com/css2?family=Noto+Serif:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&display=swap');
:root{
  --primary: #eb4a19;
  --secondary: #949696;
  --error: #ff4a4a;
}

/* base styles & title */
body{
  font-family: "Noto Serif";
  color: var(--secondary);
  background: #D2D5D6;
}
.App{
  max-width: 960px;
  margin: 0 auto;
}

.App video{
  margin: 120px auto;
  max-width: 100%;
  margin-top: 9px;
  
}
/*video style*/
.App h4{
  text-align: center;
  margin-top: 150px;
  border-top: 1px solid var(--primary);
  font-size: 2rem;
  padding-top: 50px;
 
}

.App .wideo{
  border-bottom: 1px solid var(--primary);
  
}

.video:hover {
  background: red;
}


.title h1{
  color: var(--primary);
  font-size: 2rem;
  letter-spacing: 2px;
  font-weight: normal;
  font-family: 'Dancing Script';
}

.title h2, .title p{
  text-align: center;
  
}
.title h2{
  margin-top: 60px;
  font-size: 2.6rem;
}

.title p{
  color: rgb(80, 80, 80);
  font-family: 'Dancing Script';
  font-weight: 150;
  font-size: larger;
}


/* upload form styles */
form{
 
  margin: 30px auto 30px;
  text-align: center;
}
label input{
  height: 0;
  width: 0;
  opacity: 0;
}
label{
  display: block;
  width: 30px;
  height: 30px;
  border: 1px solid var(--primary);
  border-radius: 50%;
  margin: 100px auto;
  line-height: 30px;
  color: var(--primary);
  font-weight: bold;
  font-size: 24px;
}
label:hover{
  background: var(--primary);
  color: rgba(255, 255, 255, 0.815);
}
.output{
  height: 60px;
  font-size: 0.8rem;
}
.error{
  color: var(--error);
}

/* progress bar styles */
.progress-bar{
  height: 5px;
  background: var(--primary);
  margin-top: 20px;
}

/* image grid styles */
.img-grid{
  margin: 20px auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 40px;
}
.img-wrap{
  overflow: hidden;
  height: 0;
  padding: 50% 0;
  /* padding controls height, will always be perfectly square regardless of width */
  position: relative;
  opacity: 0.8;
}
.img-wrap img{
  min-width: 100%;
  min-height: 100%;
  max-width: 150%;
  position: absolute;
  top: 0;
  left: 0;
}

/* modal styles */
.backdrop{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
}
.backdrop img{
  display: block;
  max-width: 60%;
  max-height: 80%;
  margin: 60px auto;
  box-shadow: 3px 5px 7px rgba(0,0,0,0.5);
  border: 3px solid rgba(255, 255, 255, 0.801);
}

/*footer style*/
.footer-text {
  
  color: #006666 !important;
  border-color: cornsilk;
  border-bottom-color: cornsilk;
  text-shadow: 1cm cornsilk;
}

a{
  color:black;
}



.footer-div {
  margin-top: 2rem;
  text-align: center;
  border-top: 1px solid var(--primary);
  
}

a:hover {
  background: #8f8484e3;
}

